import React from 'react';
import { Typography } from "@mui/material";
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import EmailIcon from '@mui/icons-material/Email';
import IconButton from '@mui/material/IconButton';

type ProfileCardProps = {
  name: string;
  title: string;
  profession: string;
  university: string;
  company: string;
  linkedinUrl: string;
  email: string;
  imageUrl: string;
  universityLogo: string;
  companyLogo: string;
};

const ProfileCard: React.FC<ProfileCardProps> = ({
  name,
  title,
  profession,
  university,
  company,
  linkedinUrl,
  email,
  imageUrl,
  universityLogo,
  companyLogo,
}) => {
  return (
    <div style={{
      display: 'flex',
      border: '1px solid #ccc',
      borderRadius: '10px',
      padding: '10px',
      width: '84%',
      marginTop: '20px',
      marginRight: '5%',
      marginLeft: '5%',
      alignItems: 'center',
      flexDirection: 'column'
    }}>
      <img 
        src={imageUrl} 
        alt={name} 
        style={{
          width: '150px',
          height: '175px',
          borderRadius: '10px',
        }} 
      />
      <div style={{ textAlign: 'center' }}>
        <h2>{name}</h2>
        <p style={{ margin: '0', fontWeight: 'bold' }}>{title}</p>
        <p style={{ margin: '5px 0' }}>{profession}</p>
        <div style={{ display: 'flex', alignItems: 'center', marginTop: '10px', height: '50px', justifyContent: 'center' }}>
          <img 
            src={universityLogo} 
            alt={university} 
            style={{ width: '100px', marginRight: '10px' }} 
          />
          {/* vertical line */}
          <div style={{ width: '1px', height: '100%', backgroundColor: 'black' }} />
          <img 
            src={companyLogo} 
            alt={company} 
            style={{ width: '100px', marginLeft: '5px' }} 
          />
        </div>
        {/* align the buttons to the middle of the div */}
        <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', marginTop: '10px' }}>
            <IconButton style={{width: '30px', height: '30px'}} onClick={() => {
              window.location.href = linkedinUrl;
            }}> 
              <LinkedInIcon />
            </IconButton>
            <IconButton style={{width: '30px', height: '30px'}} onClick={() => {
              window.location.href = `mailto:${email}`;
            }}> 
              <EmailIcon />
            </IconButton>
        </div>
      </div>
    </div>
  );
};

export default ProfileCard;